import { Component } from '@angular/core';



@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})

export class AppComponent {
  distance = 0;
  timerID;
  random = () => Math.floor((Math.random() * 8) + 1);
  raiders = [
    {
      img: "http://icons.iconarchive.com/icons/icons-land/transporter/256/Taxi-Right-Yellow-icon.png",
      name: 'Taxi',
      left: 0,
      speed: 0,
      round: 0
    },
    {
      img: "http://icons.iconarchive.com/icons/icons-land/transporter/256/Car-Right-Red-icon.png",
      name: 'lada',
      left: 0,
      speed: 0,
      round: 0
    },
    {
      img: "http://icons.iconarchive.com/icons/icons-land/transporter/256/BackhoeLoader-Right-Yellow-icon.png",
      name: 'Tractor',
      left: 0,
      speed: 0,
      round: 0
    },
  ];
  start() {
    clearInterval(this.timerID);
    this.raiders.forEach(rainder => rainder.speed = this.random());
    this.timerID = setInterval(() => this.move(), 20);
  }

  move() {
    this.raiders.forEach(item => {
      if (item.left > 700) {
        item.left = -100;
        item.round += 1;
      }
      item.left += item.speed;
    })
  }

  stop() {
    clearInterval(this.timerID);
    this.raiders.forEach(item => {
      item.left = 0;
      item.round = 0;
    })
  }

}
